export default {
	async asyncData({params, payload}){
	    if(payload){
	    	console.log("PAYLOAD", payload);
	    	return { lessonPost: payload};
	    }
	    else 
	        return {
            lessonPost: await require(`~/assets/content/lesson/${params.lesson}.json`),
	    };
    },
};
